<template>
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <v-form>
      <mrc_ess_iconGroup style="top: -50px"
                         :is_disabled="disabled"
                         @handleReturn='handleReturn'
                         @handleEdit='handleEdit'
                         @handleDel='handleDel'></mrc_ess_iconGroup>
      <v-card>
        <v-card-text>
          <v-row dense>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <v-textarea v-model="params.company_desc"
                          auto-grow
                          :readonly="disabled"
                          :label="$t('label_Introduction_to_the')"
                          :row-height="24"
                          append-icon="mdi-longitude"
                          :rows="1"></v-textarea>

              <!-- :error-messages="$errors('params','company_desc',arrer)"
            @input="$v.params.company_desc.$touch()"
            @blur="$v.params.company_desc.$touch()" -->
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <!-- <mrc_ess_input :readonly="disabled"
                             :label="$t('label_Organizational_Structure') + '*'"
                             :errMsg="$errors('params','org_level_short_name',requiredRule.concat(maxLength(20)))"
                             @input="$v.params.org_level_short_name.$touch()"
                             @blur="$v.params.org_level_short_name.$touch()"
                             v-model="params.org_level_short_name"></mrc_ess_input> -->
              <mrc_ess_multiText :readonly="disabled"
                                 style="width:100%"
                                 :isNational="judegNational('org_level_short_name')"
                                 :label="$t('label_Organizational_Structure') + '*'"
                                 :error-messages="$errors('params','org_level_short_name',requiredRule.concat(maxLength(20)))"
                                 v-model="params.org_level_short_name"></mrc_ess_multiText>
              <!-- <v-text-field
                :readonly="disabled"
                label="组织机构简称*"
                :error-messages="$errors('params','org_level_short_name',requiredRule.concat(maxLength(20)))"
                @input="$v.params.org_level_short_name.$touch()"
                @blur="$v.params.org_level_short_name.$touch()"
                v-model="params.org_level_short_name"
              ></v-text-field> -->
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="2">
              <mrc_ess_prompt :disabled='disabled'
                              :rules="false"
                              :label="$t('label_The_area_code')"
                              :chips='false'
                              obj='country_mobile_prefix'
                              v-model="params.client_tel_area_code">
              </mrc_ess_prompt>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="4">
              <!-- <v-text-field :readonly="disabled"
                            :label="$t('label_number')"
                            :error-messages="$errors('params','company_phone',[{rule: 'areaRequired',msg: '请选择区号'},{rule: 'phoneVerify', msg: '请输入正确格式的号码'}])"
                            @input="$v.params.company_phone.$touch()"
                            @blur="$v.params.company_phone.$touch()"
                            v-model="params.company_phone"></v-text-field> -->
              <v-text-field :readonly="disabled"
                            :label="$t('label_number')"
                            v-model="params.company_phone"></v-text-field>
            </v-col>
            <!-- :rules="$text_rules('网址')" -->
            <!-- <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :readonly="disabled"
                            :error-messages="$errors('params','company_website',urlRule)"
                            @input="$v.params.company_website.$touch()"
                            @blur="$v.params.company_website.$touch()"
                            v-model="params.company_website"
                            :label="$t('label_The_url')"></v-text-field>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :readonly="disabled"
                            :error-messages="$errors('params','company_industry',maxLength(20))"
                            @input="$v.params.company_industry.$touch()"
                            @blur="$v.params.company_industry.$touch()"
                            v-model="params.company_industry"
                            append-icon="mdi-longitude"
                            :label="$t('label_industry')"></v-text-field>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :readonly="disabled"
                            v-model="params.company_sign_no"
                            :error-messages="$errors('params','company_sign_no',maxLength(20))"
                            @input="$v.params.company_sign_no.$touch()"
                            @blur="$v.params.company_sign_no.$touch()"
                            :label="$t('label_Business_Registration_Certificate_Number')"></v-text-field>
            </v-col> -->
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <mrc_ess_multiText :readonly="disabled"
                                 style="width:100%"
                                 :error-messages="$errors('params','company_website',urlRule)"
                                 :isNational="judegNational('company_website')"
                                 v-model="params.company_website"
                                 :label="$t('label_The_url')"></mrc_ess_multiText>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <mrc_ess_multiText :readonly="disabled"
                                 style="width:100%"
                                 :error-messages="$errors('params','company_industry',maxLength(20))"
                                 :isNational="judegNational('company_industry')"
                                 v-model="params.company_industry"
                                 :label="$t('label_industry')"></mrc_ess_multiText>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <mrc_ess_multiText :readonly="disabled"
                                 style="width:100%"
                                 v-model="params.company_sign_no"
                                 :error-messages="$errors('params','company_sign_no',maxLength(20))"
                                 :isNational="judegNational('company_sign_no')"
                                 :label="$t('label_Business_Registration_Certificate_Number')"></mrc_ess_multiText>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :readonly="disabled"
                            v-model="params.company_logo_light"
                            label="公司商标上传light"></v-text-field>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <v-text-field :readonly="disabled"
                            v-model="params.company_logo_dark"
                            label="公司商标上传dark"></v-text-field>
            </v-col>
          </v-row>
          <h3 style="text-align: left">{{$t('label_address')}}</h3>
          <v-card-text>
            <div class="address">
              <v-row dense>
                <!-- <v-col class="d-flex"
                       cols="12"
                       sm="12">
                  <v-text-field :readonly="disabled"
                                :label="$t('label_The_street_address')"
                                append-icon="mdi-longitude"
                                v-model="params.company_address_line_one"></v-text-field>
                </v-col>
                <v-col class="d-flex"
                       cols="12"
                       sm="12">
                  <v-text-field :readonly="disabled"
                                append-icon="mdi-longitude"
                                v-model="params.company_address_line_two"></v-text-field>
                </v-col>
                <v-col class="d-flex"
                       cols="12"
                       sm="12">
                  <v-text-field :readonly="disabled"
                                append-icon="mdi-longitude"
                                v-model="params.company_address_line_three"></v-text-field>
                </v-col> -->
                <v-col class="d-flex"
                       cols="12"
                       sm="12">
                  <mrc_ess_multiText :readonly="disabled"
                                     :label="$t('label_The_street_address')"
                                     :isNational="judegNational('company_address_line_one')"
                                     style="width:100%"
                                     v-model="params.company_address_line_one"></mrc_ess_multiText>
                </v-col>
                <v-col class="d-flex"
                       cols="12"
                       sm="12">
                  <mrc_ess_multiText :readonly="disabled"
                                     :isNational="judegNational('company_address_line_two')"
                                     style="width:100%"
                                     v-model="params.company_address_line_two"></mrc_ess_multiText>
                </v-col>
                <v-col class="d-flex"
                       cols="12"
                       sm="12">
                  <mrc_ess_multiText :readonly="disabled"
                                     :isNational="judegNational('company_address_line_three')"
                                     style="width:100%"
                                     v-model="params.company_address_line_three"></mrc_ess_multiText>
                </v-col>
                <v-col class="d-flex"
                       cols="12"
                       sm="4">
                  <mrc_ess_prompt :rules="false"
                                  :disabled='disabled'
                                  :label="$t('label_city')"
                                  :obj='cityObj'
                                  v-model="params.company_address_city_id">
                  </mrc_ess_prompt>
                </v-col>

                <v-col class="d-flex"
                       cols="12"
                       sm="4">
                  <mrc_ess_prompt :rules="false"
                                  :disabled='disabled'
                                  :label="$t('label_provinces')"
                                  @getData='params.company_address_city_id = ""'
                                  :obj='provinceObj'
                                  v-model="params.company_address_province_id">
                  </mrc_ess_prompt>
                </v-col>
                <v-col class="d-flex"
                       cols="12"
                       sm="4">
                  <mrc_ess_prompt :rules="false"
                                  :disabled='disabled'
                                  :label="$t('label_countries')"
                                  @getData='params.company_address_province_id = ""'
                                  obj='mrbase_common_geo_country'
                                  v-model="params.company_address_country_id">
                  </mrc_ess_prompt>
                </v-col>
                <v-col class="d-flex"
                       cols="12"
                       sm="12">

                  <!-- <v-text-field v-model="params.company_address_zip_code"
                                :readonly='disabled'
                                :label="$t('label_Zip_code')"></v-text-field> -->
                  <mrc_ess_multiText v-model="params.company_address_zip_code"
                                     :isNational="judegNational('company_address_zip_code')"
                                     :readonly='disabled'
                                     :label="$t('label_Zip_code')"></mrc_ess_multiText>
                </v-col>
              </v-row>
            </div>
          </v-card-text>
        </v-card-text>
        <mrc_ess_submitBtn @cancel='cancel'
                           v-if="!disabled"
                           @success='success'></mrc_ess_submitBtn>
      </v-card>
    </v-form>
  </mrc_ess_skLoaders>

</template>

<script>
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
import { add_update_baseData, del_baseData, find_baseData } from '@/service/api/company_set'
export default {
  components: {},
  props: {},
  data () {
    return {
      disabled: true,
      skLoading: true,
      org_level_short_name: '',
      params: {
        uuid: '',
        org_level_uuid: '',
        org_level_short_name: '',
        client_tel_area_code: '',
        company_desc: '',
        company_phone: '',
        company_website: '',
        company_industry: '',
        company_sign_no: '',
        company_logo_dark: '',
        company_address_line_one: '',
        company_address_line_two: '',
        company_address_line_three: '',
        company_address_city_id: null,
        company_address_country_id: null,
        company_address_province_id: null,
        company_address_zip_code: ''
      }
    };
  },
  validations: {
    params: {
      company_website: {
        url
      },
      company_industry: {
        maxLength: maxLength(20)
      },
      company_sign_no: {
        maxLength: maxLength(20)
      },
      org_level_short_name: {
        maxLength: maxLength(20),
        required
      },
      company_phone: {
        // areaRequired (value) {
        //   if (value) {
        //     return this.params.client_tel_area_code ? true : false
        //   } else {
        //     return true
        //   }
        // },
        // phoneVerify (value) {
        //   if (!value)
        //     return true
        //   const regex = this.$myVerify.phone
        //   return regex.test(this.params.client_tel_area_code + value)
        // }
      }
    }
  },
  created () { },
  async mounted () {
    if (!this.$store.state.iconstatus) {
      this.disabled = false
    }
    await this.national('mrbase_sys_company')
    this.params.org_level_uuid = this.$store.state.uuid
    await this.getData()
  },
  methods: {
    async getData () {
      this.skLoading = true
      const getData = await find_baseData({
        org_level_uuid: this.params.org_level_uuid
      })
      if (getData.code == 200 && Object.keys(getData.data).length > 0) {
        this.params = getData.data
      }
      this.skLoading = false
    },
    cancel () {
      this.$myConfirm({ title: '提示', content: '是否放弃修改' })
        .then(async () => {
          await this.getData()
          this.disabled = !this.disabled
        }).catch(() => { })
    },
    async success () {
      this.$v.params.$touch()
      if (this.$v.params.$error) return

      if (!this.params.company_address_city_id) {
        this.params.company_address_city_id = null
      }
      if (!this.params.company_address_country_id) {
        this.params.company_address_country_id = null
      }
      if (!this.params.company_address_province_id) {
        this.params.company_address_province_id = null
      }
      // console.log(this.params.company_desc.length)
      // return
      const submit = await add_update_baseData({
        uuid: this.params.uuid,
        org_level_uuid: this.params.org_level_uuid,
        org_level_short_name: this.params.org_level_short_name,
        company_desc: this.params.company_desc,
        client_tel_area_code: this.params.client_tel_area_code,
        company_phone: this.params.company_phone,
        company_website: this.params.company_website,
        company_industry: this.params.company_industry,
        company_sign_no: this.params.company_sign_no,
        company_logo_light: this.params.company_logo_light,
        company_logo_dark: this.params.company_logo_dark,
        company_address_line_one: this.params.company_address_line_one,
        company_address_line_two: this.params.company_address_line_two,
        company_address_line_three: this.params.company_address_line_three,
        company_address_city_id: this.params.company_address_city_id,
        company_address_province_id: this.params.company_address_province_id,
        company_address_country_id: this.params.company_address_country_id,
        company_address_zip_code: this.params.company_address_zip_code,
      })
      if (submit.code == 200) {
        this.$store.commit('newsTips', this.$t('message_The_submission_was_successful'))
        this.disabled = !this.disabled
      }
    },
    handleReturn () {
      this.$router.go(-1)
    },
    handleEdit () {
      this.disabled = !this.disabled
    },
    // 删除
    handleDel () {
      this.$myConfirm({ title: '提示', content: '是否删除' })
        .then(async () => {
          const del = await del_baseData({
            org_level_uuid: this.params.org_level_uuid
          })
          if (del.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.go(-1)
          }
        }).catch(() => { })
    },
  },
  computed: {
    provinceObj () {
      return {
        prompt_name: 'mrbase_common_geo_province',
        country: this.params.company_address_country_id
      }
    },
    cityObj () {
      return {
        prompt_name: 'mrbase_common_geo_city',
        province: this.params.company_address_province_id
      }
    },
    verifyPhone () {
      return this.params.client_tel_area_code + this.params.company_phone
    }
  },
  watch: {},
};
</script>

<style scoped>
.address {
  border: 1px solid #e9e9e9;
  border-radius: 10px;
  padding: 20px 20px;
}
</style>
